import style from "./notify.css";

interface MsgProps {
  title: string;
  buttonLabel: string;
  buttonOnClick: () => void;
}

const Msg = ({ title, buttonLabel, buttonOnClick }: MsgProps) => (
  <div className={style.wrapper}>
    <p>{title}</p>
    <button type="button" onClick={buttonOnClick}>
      {buttonLabel}
    </button>
  </div>
);

export { Msg };
export { toast as notify } from "react-toastify";
